<template>
	<section class="ui-table box vertical" :style="boxStyle">
		<section v-if="show_head && (get || $slots.default || renderHeader)" class="head">
			<el-form ref="form" :inline="true" :model="self_form" size="mini" :disabled="elForm.disabled">
				<input type="text" style="display: none;">
				<el-form-item
					v-for="(item, key) in formConf"
					:key="key"
					:label="item.label"
					:prop="key"
					:title="item.label || item.deepGet('config', 'placeholder')"
				>
					<ui-auto v-if="item.render" v-bind="{render: item.render, form, formConf, Self:this}" />
					<ui-auto-form-item v-else v-model="self_form[key]" :type="item.type" v-bind="item" @keyup.native.enter="search" />
				</el-form-item>
				<el-form-item v-if="get && (form_conf_length >= 1 || self_total == 0) || $slots.default || renderHeader">
					<template v-if="get">
						<el-button v-if="form_conf_length >= 1 || self_total == 0" type="primary" @click="_ => search(0)">{{ search_name || t('ui.table.search') }}</el-button>
						<el-button v-if="form_conf_length >= 1" @click="reset">{{ t('ui.table.reset') }}</el-button>
					</template>
					<slot v-bind="{Self:this, Table:$refs.table}" />
					<ui-auto v-if="renderHeader" :render="renderHeader" v-bind="{Self:this, Table:$refs.table}" />
				</el-form-item>
			</el-form>
		</section>
		<section class="auto">
			<el-table
				ref="table"
				v-loading="loading"
				:data="data"
				:highlight-current-row="!show_select"
				:row-key="rowKey"
				:current-row-key="currentRowKey"
				element-loading-background="rgba(0, 0, 0, 0.1)"
				height="100%"
				size="mini"
				border
				style="width: 100%;"
				v-bind="$attrs['table-attrs'] || {}"
				v-on="listeners"
			>
				<!-- 用于展开 -->
				<slot name="expand" />

				<!-- 用于选择 -->
				<template v-if="show_select">
					<!-- 多选时 -->
					<el-table-column
						v-if="multiple"
						align="center"
						fixed="left"
						header-align="center"
						type="selection"
						:selectable="selectable"
						:reserve-selection="true"
						width="50"
					/>
					<!-- 单选时 -->
					<el-table-column
						v-else
						:label="t('ui.table.check')"
						align="center"
						fixed="left"
						header-align="center"
						width="60"
					>
						<template v-slot="scope">
							<el-radio :value="(value||{})[props.id]" :label="scope.row[props.id]" @change.native="$emit('input', clone(scope.row))"><pre /></el-radio>
						</template>
					</el-table-column>
				</template>

				<!-- 索引列 -->
				<el-table-column
					v-if="show_index"
					:index="index => pageSize * (currentPage - 1) + 1 + index"
					label="#"
					align="center"
					fixed="left"
					header-align="center"
					width="80K"
					type="index"
				/>

				<!-- 常规列 -->
				<template v-for="(item, key) in columns">
					<el-table-column
						v-if="$license(item.power, 'some')"
						:key="key"
						:prop="key"
						:show-overflow-tooltip="true"
						header-align="center"
						v-bind="item"
					/>
				</template>
			</el-table>
		</section>
		<section v-if="self_total != 0" class="foot">
			<el-pagination
				ref="page"
				:current-page="currentPage"
				:page-size="pageSize"
				:page-sizes="page_sizes"
				:total="self_total"
				background
				layout="total, sizes, prev, pager, next, jumper, slot"
				@current-change="page => (currentPage = page)"
				@next-click="page => (currentPage = page)"
				@prev-click="page => (currentPage = page)"
				@size-change="size => (pageSize = size)"
			>
				<span>
					<i class="el-icon-refresh" @click="search" />
				</span>
			</el-pagination>
		</section>
	</section>
</template>

<script>
import uiAuto from '../auto'
import uiAutoFormItem from '../autoFormItem'
import emitter from 'element-ui/lib/mixins/emitter'
import locale from '../../src/mixins/locale'
import { clone } from '../../src/utils'
export default {
	name: 'UiTable',
	components: { uiAuto, uiAutoFormItem },
	mixins: [emitter, locale],
	inject: {
		elForm: {
			default: ''
		},
		elFormItem: {
			default: ''
		}
	},
	props: {
		show_index: {
			// 是否显示索引列
			type: Boolean,
			default: true
		},
		rowKey: {
			type: String,
			default: 'id'
		},
		currentRowKey: {
			type: [String, Number],
			default: ''
		},
		columns: {
			// 列配置
			type: Object,
			default: _ => ({}),
			required: true
		},
		data: {
			// 表格数据
			type: Array,
			default: _ => []
		},
		get: {
			// 更新数据方法
			type: Function
		},
		renderHeader: {
			type: Function
		},
		boxStyle: {
			// 样式
			type: Object,
			default: _ => ({})
		},
		selectable: {
			type: Function,
			default: _ => _ => true
		},
		/* -----------------------------------------顶部搜索条件----------------------------------- */
		show_head: {
			// 是否显示顶部搜索区域
			type: Boolean,
			default: true
		},
		formConf: {
			// 表单配置
			type: Object,
			default: _ => ({})
		},
		form: {
			// 表单数据
			type: Object,
			default: _ => ({})
		},
		search_name: {
			// 搜索按键名称
			type: String,
			default: ''
		},
		/* -------------------------------------------分页-------------------------------------- */
		total: {
			// 总条数
			type: Number,
			default: 0
		},
		current_page: {
			// 当前页码
			type: Number,
			default: 1
		},
		page_size: {
			// 单页条数
			type: Number,
			default: 20
		},
		page_sizes: {
			// 页码配置
			type: Array,
			default: _ => [20, 50, 100, 200]
		},
		page_config: {
			type: Object,
			default: _ => ({
				current_page: 'current_page',
				page_size: 'page_size'
			})
		},
		/* -----------------------------------------选择-------------------------------------- */
		value: {
			// 选择集
			type: [Array, String, Number, Object],
			default: _ => []
		},
		show_select: {
			// 是否显示选择列
			type: Boolean,
			default: false
		},
		multiple: {
			// 是否多选
			type: Boolean,
			default: false
		},
		disabled: {
			// 是否禁止选择
			type: Boolean,
			default: false
		},
		props: {
			// value数据属性配置
			type: Object,
			default: _ => ({
				id: 'id',
				name: 'name'
			})
		}
	},
	data() {
		return {
			currentPage: this.current_page,
			pageSize: this.page_size,
			loading: false,
			self_form: {},
			self_total: 0
		}
	},
	computed: {
		input_disabled() {
			// 禁止输入
			return this.disabled || (this.elForm || {}).disabled
		},
		data_length() {
			// 当前页面数据长度
			return (this.data || []).length
		},
		form_conf_length() {
			// 头部搜索条件个数
			return Object.keys(this.formConf).length
		},
		value_keys() {
			return this.value.map(item => item[this.props.id])
		},
		all_keys() {
			return this.data.map(item => item[this.props.id])
		},
		exists_index() {
			return this.value_keys.map(item => this.all_keys.indexOf(item)).filter(item => item >= 0)
		},
		value_id_exist() {
			const value = this.value
			return value && value[this.props.id]
		},
		listeners() {
			const listeners = {
				...this.$attrs.tableEvents || {},
				...this.$listeners
			}
			return {
				...listeners,
				'header-dragend': (...args) => { this.$refs.table.doLayout(), listeners['header-dragend'] && listeners['header-dragend'](...args) },
				select: (...args) => { this.select(...args), listeners.select && listeners.select(...args) },
				'select-all': (...args) => { this.selectAll(...args), listeners['select-all'] && listeners['select-all'](...args) },
				'row-click': (row, column, event) => {
					listeners['update:current-row-key'] && listeners['update:current-row-key'](row[this.rowKey])
					listeners['row-click'] && listeners['row-click'](row, column, event)
				}
			}
		}
	},
	watch: {
		current_page(val) {
			this.currentPage = val
		},
		page_size(val) {
			this.pageSize = val
		},
		data_length(val) {
			if (val == 0 && this.currentPage >= 2) this.currentPage -= 1
		},
		currentPage() {
			this.search()
		},
		pageSize() {
			this.search()
		},
		form: {
			deep: true,
			handler(val) {
				this.initializeForm()
				this.search(0)
			}
		},
		value(val) {
			this.$emit('input', val)
			this.dispatch('ElFormItem', 'el.form.change', [val])

			if (this.multiple) {
				this.$nextTick(_ => this.data.forEach((item, key) => this.$refs.table.toggleRowSelection(item, this.exists_index.indexOf(key) >= 0)))
			}
		},
		total(val) {
			this.self_total = val
		},
		data: {
			immediate: true,
			handler(val) {
				if (val.length >= 1 && this.rowKey) {
					const row = this.currentRowKey ? val.find(item => item[this.rowKey] == this.currentRowKey) : val[0]
					if (row) {
						this.$nextTick(_ => this.$refs.table.setCurrentRow(row))
						this.listeners['update:current-row-key'] && this.listeners['update:current-row-key'](row[this.rowKey])
					}
				}
			}
		},
		currentRowKey: {
			immediate: true,
			handler(val) {
				const { rowKey } = this,
					row = this.data.find(item => item[rowKey] == val)

				if (rowKey && val && row) {
					this.$nextTick(_ => this.$refs.table.setCurrentRow(row))
					this.listeners['update:current-row-key'] && this.listeners['update:current-row-key'](row[this.rowKey])
				}
			}
		}
	},
	created() {
		this.initializeForm()
		this.search()
	},
	methods: {
		clone,
		search(state) {
			if (state === 0) {
				const { currentPage } = this
				if (currentPage != 1) {
					this.currentPage = 1
					return
				}
			}

			this.loading = true

			const { get = _ => Promise.resolve() } = this

			return get({
				[this.page_config.current_page]: this.currentPage,
				[this.page_config.page_size]: this.pageSize,
				...clone(this.self_form)
			}, this).then(_ => {
				this.$nextTick(_ => {
					setTimeout(_ => this.loading = false, 300)
					this.$refs.table.doLayout()

					if (this.multiple) {
						this.data.forEach((item, key) => this.$refs.table.toggleRowSelection(item, this.exists_index.indexOf(key) >= 0))
					}
				})
			})
		},
		reset() {
			this.$refs.form.resetFields()
			this.$emit('resetEvent')
			return this.search(0)
		},
		initializeForm() {
			// 初始化form,若formConf的各项属性在form不存在对应，则进行填充
			const { form, self_form, formConf } = this
			Object.keys(formConf).forEach(name => {
				const origin_value = form[name],
					config_value = formConf[name].value == undefined ? null : clone(formConf[name].value)
				this.$set(self_form, name, origin_value == undefined ? config_value : origin_value)
			})

			Object.keys(form).forEach(name => { if (!(name in self_form)) this.$set(self_form, name, form[name]) })
		},
		select(selection, row) {
			// multiple模式下选择触发
			// 多选时的选择动作触发
			const if_add = selection.indexOf(row) >= 0,
				index = this.value_keys.indexOf(row[this.props.id]),
				exist = index >= 0

			if (if_add) {
				if (!exist) this.value.push(row)
			} else {
				if (exist) this.value.splice(index, 1)
			}
		},
		selectAll(selection) {
			// multiple模式下选择所有
			const key_name = this.props.id
			if (selection.length > 0) {
				selection.forEach(item => {
					const exist = this.value_keys.indexOf(item[key_name]) >= 0
					if (!exist) this.value.push(item)
				})
			} else {
				this.data.forEach(item => {
					const index = this.value_keys.indexOf(item[key_name]),
						exist = index >= 0
					if (exist) this.value.splice(index, 1)
				})
			}
		}
	}
}
</script>

<style lang="scss" scoped>
.ui-table {
	padding: 10px;

	& > .head {
		.el-form-item {
			margin-bottom: 10px;
		}
	}

	& > .foot {
		padding-top: 10px;
		//background-color: #F8F8F8;
		text-align: center;

		.el-icon-refresh {
			margin-left: 10px;
			font-size: 1.5rem;
			line-height: 28px;
			cursor: pointer;
		}
	}
}
</style>