<template>
	<el-scrollbar ref="scrollBar" class="ui_tabs" @mousewheel.native="mousewheel">
		<el-tag
			v-for="item in routes"
			:ref="item.full_path"
			:key="item.full_path"
			:title="item.title"
			:class="{
				selected: item.full_path == full_path
			}"
			:disable-transitions="true"
			:effect="item.full_path == full_path ? 'light' : 'plain'"
			:style="item.full_path == full_path ? {...normalStyle, ...activeStyle} : normalStyle"
			size="medium"
			type="info"
			@click="$updateRoute(item.full_path, item.title)"
		>
			<span>&bull;</span>
			{{ item.title }}
			<i v-if="routes.length >= 2" class="el-icon-close" @click="$closeRoute({full_path:item.full_path, routes})" />
		</el-tag>
	</el-scrollbar>
</template>

<script>
export default {
	name: 'UiTabs',
	props: {
		routes: { // 打开路由缓存
			type: Array,
			required: true
		},
		normalStyle: { // 常规样式
			type: Object,
			default: _ => ({})
		},
		activeStyle: { // 选中样式
			type: Object,
			default: _ => ({})
		},
		beforeClose: { // 关闭前回调
			type: Function
		},
		afterClose: { // 关闭后回调
			type: Function
		}
	},
	computed: {
		full_path() {
			return this.$route.fullPath
		},
		wrap() {
			return this.$refs.scrollBar.wrap
		}
	},
	watch: {
		$route: {
			handler(val) {
				this.$nextTick(_ => this.scrollTo(val.fullPath))
			},
			immediate: true
		}
	},
	methods: {
		mousewheel(e) {
			const vector = e.wheelDelta || e.detail,
				step = vector / Math.abs(vector)
			this.wrap.scrollLeft -= 100 * step
		},
		scrollTo(full_path) {
			if (!this.$refs.deepGet(full_path, 0)) return

			// 若指定标签不在可视区域，则以最小的滚动距离出现在可视区域
			const { left: L, right: R } = this.wrap.getBoundingClientRect(),
				{ left: l, right: r } = this.$refs[full_path][0].$el.getBoundingClientRect(),
				_l = l - 5 - L, // tag左边界与容器左边界距离
				_r = r + 5 - R, // tag右边界与容器右边界距离
				move = _l < 0 ? _l : _r > 0 ? _r : 0

			if (move == 0) return

			const time = 300, // 动画时间
				n = 10 // 循环次数
			let i = 0,
				interval = setInterval(_ => {
					if (i++ >= n) {
						clearInterval(interval)
						interval = null
						return
					}
					this.wrap.scrollLeft += move / n
				}, time / 10)
		}
	}
}
</script>

<style lang="scss">
.ui_tabs {
	overflow: hidden;
	white-space: nowrap;

	& > .el-scrollbar__wrap > .el-scrollbar__view > .el-tag {
		position: relative;
		//min-width: 80px;
		max-width: 300px;
		padding-right: 23px;
		margin: 6px 5px 2px;
		border-color: transparent;
		//white-space: nowrap;
		overflow: hidden;
		text-overflow: ellipsis;
		color: #555;
		background: #eee;
		cursor: pointer;
		user-select: none;

		&:hover {
			background: #ddd;
		}

		&.selected {
			font-weight: bold;
			color: #fff;
			background-color: #008ccf;

			& > span {
				display: inline-block;
			}
		}

		& > span {
			display: none;
		}

		& > i {
			position: absolute;
			top: 50%;
			right: 4px;
			display: none;
			font-size: 16px;
			transform: translate(0, -50%);
			transition: transform 0.5s;

			&:hover {
				transform: translate(0, -50%) rotate(90deg);
			}
		}

		&:hover > i {
			display: inline-block;
		}
	}
}
</style>