import main from './main.vue'

const
	CenterDialog = el => {
		if (window.getComputedStyle(el).display == 'none') return

		const
			{ clientHeight } = document.documentElement,
			elDialog = el.firstChild,
			elDialogHeader = elDialog.childNodes[0],
			elDialogBody = elDialog.childNodes[1],
			elDialogFooter = elDialog.childNodes[2],
			elDialogBodyStyle = window.getComputedStyle(elDialogBody),
			margin = elDialog.getAttribute('class').indexOf('is-fullscreen') < 0 ? 10 : 0,
			height1 = elDialogHeader.getBoundingClientRect ? elDialogHeader.getBoundingClientRect().height : 0,
			height2 = elDialogFooter.getBoundingClientRect ? elDialogFooter.getBoundingClientRect().height : 0

		elDialogBody.style.maxHeight = `${clientHeight - margin * 2 - height1 - height2 - parseFloat(elDialogBodyStyle.paddingTop) - parseFloat(elDialogBodyStyle.paddingBottom)}px`
		elDialog.style.transform = ''
	},
	addEvent = (el, name, fn) => el.attachEvent ? el.attachEvent('on' + name, fn) : el.addEventListener(name, fn)

main.install = function(Vue) {
	Vue.component(main.name, main)

	// 动态渲染弹出框
	Vue.prototype.$dialog = data => {
		const instance = new Vue({
			data() {
				return {
					visible: true
				}
			},
			render(h) {
				return <ui-dialog
					{...{
						props: {
							...data,
							visible: this.visible
						},
						attrs: {
							'auto-height': data.autoHeight
						},
						on: {
							'update:visible': e => this.visible = e,
							closed: e => this.$destroy()
						}
					}}
				/>
			}
		})
		instance.$mount()
		return instance
	}

	Vue.directive('drag-ui-dialog', {
		inserted: function(el, binding) {
			setTimeout(_ => CenterDialog(el), 0)
		}
	})

	addEvent(window, 'resize', function(e) {
		[...document.querySelectorAll('.ui-dialog.el-dialog__wrapper')].forEach(el => CenterDialog(el))
	})

	{
		const
			getTranslate = el => {
				const arr = (window.getComputedStyle(el).transform || 'matrix(1,0,0,1,0,0)').replace(/[matrix()\s]/gi, '').split(',').map(v => Number(v))
				return [arr[4], arr[5]]
			},
			getRect = el => el.getBoundingClientRect(),
			clearSelection = _ => 'getSelection' in window ? window.getSelection().removeAllRanges() : document.selection.empty()

		let
			target,
			mousedownX = 0,
			mousedownY = 0,
			start = [0, 0]

		addEvent(window, 'mousedown', function(e) {
			clearSelection()

			const
				{ pageX, pageY } = e,
				current = e.target,
				parent = current.parentNode,
				grand_parent = parent.parentNode

			if (!grand_parent || (grand_parent.getAttribute('class') || '').indexOf('ui-dialog') < 0) return false

			target = parent
			mousedownX = pageX
			mousedownY = pageY

			start = getTranslate(target)
		})

		addEvent(window, 'mousemove', function(e) {
			if (!target) return

			const
				{ pageX, pageY } = e,
				deltaX = pageX - mousedownX,
				deltaY = pageY - mousedownY

			target.style.transform = `translate(${start[0] + deltaX}px, ${start[1] + deltaY}px)`
		})

		addEvent(window, 'mouseup', function(e) {
			if (!target) return

			const
				end = getTranslate(target),
				{ clientWidth, clientHeight } = document.documentElement,
				{ left, top, right, bottom } = getRect(target),

				x = left < 0 ? -left : right > clientWidth ? clientWidth - right : 0,
				y = top < 0 ? -top : bottom > clientHeight ? clientHeight - bottom : 0

			target.style.transform = `translate(${end[0] + x}px, ${end[1] + y}px)`

			target = undefined
			mousedownX = 0
			mousedownY = 0
			start = [0, 0]
		})
	}
}

export default main