<script>
export default {
	name: 'UiAutoFormItem',
	inheritAttrs: false,
	props: {
		type: {
			type: String,
			required: true
		},
		value: {
			required: true
		},
		config: {
			type: Object,
			default: _ => ({})
		}
	},
	render(h) {
		const { type, config, $attrs, value } = this

		let sub

		switch (type) {
			case 'el-select':
				sub = config.lists.map(item => {
					const id = config.deepGet('props', 'id') || 'id',
						name = config.deepGet('props', 'name') || 'name',
						obj = { ...item }

					delete obj[id]
					delete obj[name]
					return <el-option {...{
						attrs: {
							...obj,
							key: item[id],
							label: item[name] || item[id],
							value: item[id]
						}
					}} />
				})
				break
			case 'el-radio-group':
				sub = config.lists.map(item => {
					const id = config.deepGet('props', 'id') || 'id',
						name = config.deepGet('props', 'name') || 'name',
						obj = { ...item }

					delete obj[id]
					delete obj[name]

					return <el-radio {...{
						attrs: {
							...obj,
							key: item[id],
							label: item[id]
						}
					}}>
						{item[name] || item[id]}
					</el-radio>
				})
				break
			case 'el-checkbox':
				sub = config.text || ''
				break
			case 'el-checkbox-group':
				if (!Array.isArray(value)) throw '组件<el-checkbox-group>的value必须是数组'
				sub = config.lists.map(item => {
					const id = config.deepGet('props', 'id') || 'id',
						name = config.deepGet('props', 'name') || 'name',
						obj = { ...item }

					delete obj[id]
					delete obj[name]

					return <el-checkbox {...{
						attrs: {
							...obj,
							key: item[id],
							label: item[id]
						}
					}}>
						{item[name] || item[id]}
					</el-checkbox>
				})
				break
			default:
				sub = null
		}

		const { style = {}, ...other } = config

		return h(type, {
			attrs: {
				...other,
				clearable: type == 'el-input' ? true : config.clearable,
				placeholder: config.placeholder || $attrs.label,
				value
			},
			style: style,
			on: {
				input: v => this.$emit('input', v)
			}
		}, [sub])
	}
}
</script>