<script>
/*
	el-dialog的中间件,用法一模一样
*/

export default {
	name: 'UiDialog',
	functional: true,
	render(h, ctx) {
		const { data, slots, props } = ctx,
			{ renderTitle, renderContent, renderFooter } = props,
			nodeTitle = renderTitle ? renderTitle(h) : slots().title,
			nodeContent = renderContent ? renderContent(h) : slots().default,
			nodeFooter = renderFooter ? renderFooter(h) : slots().footer

		return <el-dialog
			{...{
				...data,
				...props.visible
					? {
						directives: [
							{ name: 'drag-ui-dialog', value: data }
						]
					}
					: null
			}}
			class="ui-dialog"
			top="0"
			append-to-body={true}
			modal-append-to-body={true}
			close-on-press-escape={false}
			close-on-click-modal={false}
		>
			{nodeTitle ? <template slot="title">{nodeTitle}</template> : null}
			{nodeContent}
			{nodeFooter ? <template slot="footer">{nodeFooter}</template> : null}
		</el-dialog>
	}
}
</script>

<style lang="scss">
.ui-dialog {
	overflow: hidden !important;

	& > .el-dialog {
		position: relative;
		top: 50%;
		display: flex;
		flex-flow: column nowrap;
		transform: translate(0, -50%);

		& > .el-dialog__header {
			cursor: move;
		}

		& > .el-dialog__body {
			flex: auto;
			overflow: auto;
		}
	}

	&[auto-height] > .el-dialog {
		height: calc(100% - 20px);
	}
}
</style>