import formDialog from './packages/formDialog'
import form from './packages/form'
import selectTable from './packages/selectTable'
import table from './packages/table'
import auto from './packages/auto'
import autoFormItem from './packages/autoFormItem'
import recursiveMenuItem from './packages/recursiveMenuItem'
import tabs from './packages/tabs'
import tree from './packages/tree'
import dialog from './packages/dialog'
import vxeTable from './packages/vxeTable'
import selectVxeTable from './packages/selectVxeTable'

import './src/style/index.scss'

import locale from './src/locale'

const components = [
		formDialog,
		form,
		selectTable,
		table,
		auto,
		autoFormItem,
		recursiveMenuItem,
		tabs,
		tree,
		dialog,
		vxeTable,
		selectVxeTable
	],

	install = function (Vue, options = {}) {
		components.forEach(component => {
			Vue.use(component)
		})

		locale.use(options.locale)
		locale.i18n(options.i18n)
	}

if (typeof window !== 'undefined' && window.Vue) {
	install(window.Vue)
}

export default { install }
export {
	formDialog,
	form,
	selectTable,
	table,
	auto,
	autoFormItem,
	recursiveMenuItem,
	tabs,
	tree,
	dialog,
	vxeTable,
	selectVxeTable
}