<script>
import uiAutoFormItem from '../autoFormItem'
import { clone } from '../../src/utils'
export default {
	name: 'UiForm',
	components: { uiAutoFormItem },
	props: {
		inline: {
			type: Boolean,
			default: false
		},
		labelPosition: {
			type: String,
			default: 'right'
		},
		labelWidth: {
			type: String,
			default: '100px'
		},
		disabled: {
			type: Boolean,
			default: false
		},
		form: {
			type: Object,
			default: _ => ({})
		},
		formConf: {
			// 表单各字段配置
			type: Object,
			default: _ => ({})
		},
		temp: {
			type: Object,
			default: _ => ({})
		}
	},
	data() {
		return {
			selfFormConf: {},
			selfForm: {}
		}
	},
	watch: {
		formConf: {
			deep: true,
			immediate: true,
			handler(val) {
				this.selfFormConf = clone(val)
			}
		},
		form: {
			deep: true,
			immediate: true,
			handler(val) {
				this.selfForm = clone(val)
			}
		}
	},
	created() {
		this.initializeModel()
	},
	methods: {
		// 初始化form,若formConf的各项属性在form不存在对应，则进行填充
		initializeModel() {
			const { selfForm, selfFormConf } = this
			Object.keys(selfFormConf).forEach(key => {
				const origin_value = selfForm[key],
					config_value = selfFormConf[key].value == undefined ? null : clone(selfFormConf[key].value)
				this.$set(selfForm, key, origin_value == undefined ? config_value : origin_value)
			})
		}
	},
	render(h) {
		const { selfFormConf: formConf, disabled, $scopedSlots, selfForm: form, inline, labelPosition, labelWidth } = this,
			formItems = Object.keys(formConf).map(key => {
				const item = formConf[key],
					{ type, label, required, rules = [], render = false, visible = true, width = 'auto', 'label-width': labelWidth } = item

				return <el-form-item
					{...{
						attrs: { key, label, prop: key, required, rules, 'label-width': labelWidth },
						style: {
							display: inline ? 'inline-block' : 'block',
							width
						}
					}}
					ref={key}
					v-show={visible}
				>
					{render
						? render({ form, formConf, Self: this })
						: $scopedSlots[key]
							? $scopedSlots[key]({ form, formConf, Self: this })
							: type
								? <ui-auto-form-item
									{...{
										attrs: {
											...item,
											type,
											value: form[key]
										},
										on: {
											input: e => form[key] = e
										}
									}}
								/>
								: null}
				</el-form-item>
			})

		return <el-form
			class="ui-form"
			{...{
				props: {
					disabled,
					model: form,
					labelPosition,
					labelWidth,
					inline
				}
			}}
			ref="form"
			size="mini"
		>
			<input type="text" style="display: none;" />
			{formItems}
		</el-form>
	}
}
</script>

<style lang="scss" scoped>
.ui-form.el-form > .el-form-item {
	display: inline-block;
	margin-right: 0;

	& > .el-form-item__content {
		height: 100%;
	}
}
</style>