<template>
	<div :class="{ disabled: forbidden }" :style="{ width }" :tabindex="forbidden ? 'none' : 0" class="ui-select-table" @blur="blur">
		<div class="selected-items" @click="show">
			<!-- 无数据时提示 -->
			<div v-if="!((multiple && value.length > 0) || value_id_exist)" class="tips">{{ $attrs.placeholder }}</div>

			<!-- 多选时 -->
			<template v-if="multiple">
				<span v-for="(item, key) in value" :key="item[props.id]">
					<span :title="item[props.name] || item[props.id]">{{ item[props.name] || item[props.id] }}</span>
					<i class="el-icon-circle-close" @click.stop="value.splice(key, 1)" />
				</span>
			</template>

			<!-- 单选时 -->
			<span v-else-if="value_id_exist">
				<span :title="value[props.name] || value[props.id]">{{ value[props.name] || value[props.id] }}</span>
				<i class="el-icon-circle-close" @click.stop="$emit('input', null)" />
			</span>
		</div>

		<!-- 弹出按钮 -->
		<i class="expand el-icon-arrow-down" @click="show" />

		<!-- 弹出区域 -->
		<ui-dialog class="ui-select-table-pop" :title="title" :visible.sync="visible" :width="dialogWidth" auto-height>
			<uiVxeTable
				style="height: 100%; padding: 0;"
				:style="{
					width:''
				}"
				v-bind="{
					...$props,
					...$attrs,
					show_select: true,
				}"
				v-on="$listeners"
			/>
			<span slot="footer" class="dialog-footer">
				<el-tooltip v-if="multiple" placement="top" style="float: left;">
					<div slot="content" style="max-width: 500px; line-height: 20px;">
						<span v-for="(item, key) in value" :key="key">&bull; {{ item[props.name] }}；</span>
					</div>
					<el-badge :value="value.length">
						<el-button size="mini">{{ t('ui.selectTable.selected') }}</el-button>
					</el-badge>
				</el-tooltip>
				<el-button size="mini" type="primary" @click="visible = false">{{ t('ui.selectTable.assure') }}</el-button>
			</span>
		</ui-dialog>
	</div>
</template>

<script>
import emitter from 'element-ui/lib/mixins/emitter'
import locale from '../../src/mixins/locale'
import uiVxeTable from '../vxeTable'
import uiDialog from '../dialog'
export default {
	name: 'UiSelectVxeTable',
	components: {
		uiVxeTable,
		uiDialog
	},
	mixins: [emitter, locale],
	inject: {
		elForm: {
			default: ''
		},
		elFormItem: {
			default: ''
		}
	},
	props: {
		title: {
			default: '选项'
		},
		width: {
			default: '240px'
		},
		dialogWidth: {
			default: '90%'
		},
		value: {
			// 选择集
			type: [Array, String, Number, Object],
			default: _ => []
		},
		multiple: {
			// 是否多选
			type: Boolean,
			default: false
		},
		props: {
			// value数据属性配置
			type: Object,
			default: _ => ({
				id: 'id',
				name: 'name'
			})
		},
		disabled: {
			// 是否多选
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			visible: false // 弹框显示
		}
	},
	computed: {
		value_id_exist() {
			const value = this.value
			return value && value[this.props.id]
		},
		forbidden() {
			return this.disabled || this.elFormItem.disabled || this.elForm.disabled
		}
	},
	methods: {
		show() {
			if (!this.forbidden) this.visible = true
		},
		blur() {
			this.dispatch('ElFormItem', 'el.form.change', [this.value])
		}
	}
}
</script>

<style lang="scss">
/* 校验样式 */
.el-form-item {
	&.is-error > .el-form-item__content > .ui-select-table {
		border-color: #f56c6c;
	}
}

/* 实际样式 */
.ui-select-table {
	position: relative;
	width: 240px;
	border: 1px solid #dcdfe6;
	border-radius: 4px;
	font-size: 12px;
	line-height: 22px;
	color: #606266;
	background-color: #fff;
	transition: border-color 0.2s cubic-bezier(0.645, 0.045, 0.355, 1);

	&.disabled {
		color: #c0c4cc;
		background-color: #f5f7fa;
		cursor: not-allowed;

		& > .selected-items > span > i {
			display: none;
		}
	}

	& > .selected-items {
		box-sizing: border-box;
		width: 100%;
		padding: 0 30px 0 0;

		& > .tips {
			min-height: 24px;
			margin: 3px 6px;
			overflow: hidden;
			color: #ccc;
		}

		& > span {
			display: inline-flex;
			box-sizing: border-box;
			max-width: 100%;
			height: 24px;
			padding: 0 8px;
			margin: 3px 0 3px 6px;
			border: 1px solid transparent;
			border-radius: 4px;
			overflow: hidden;
			text-overflow: ellipsis;
			background-color: #f0f2f5;

			& > span {
				overflow: hidden;
				text-overflow: ellipsis;
				white-space: nowrap;
			}

			& > i {
				margin-left: 5px;
				font-size: 15px;
				line-height: 23px;
				color: #606266;
				cursor: pointer;

				&:hover {
					color: #f56c6c;
				}
			}
		}
	}

	& > .expand {
		position: absolute;
		top: calc(50% - 7px);
		right: 5px;
		font-size: 15px;
		cursor: pointer;

		&:hover {
			font-weight: bold;
		}
	}
}
</style>

<style lang="scss">
.ui-select-table-pop > .el-dialog > .el-dialog__body {
	padding: 10px;
}
</style>