import main from './main.vue'

main.install = function(Vue) {
	Vue.component(main.name, main)

	const
		Contrustor = Vue.extend(main),
		Producer = function(props) {
			if (Object.prototype.toString.call(props) != '[object Object]') props = {}

			const instance = new Contrustor({
				propsData: { ...props, visible: true }
			})

			instance.$on('update:visible', v => instance.$destroy())// 关闭销毁
			instance.$mount()

			return instance
		}

	Vue.prototype.$formDialog = Producer
}

export default main