<template>
	<section style="height: 100%; overflow: auto;" @scroll="scroll">
		<el-tree
			ref="tree"
			v-bind="{
				...bind,
				currentNodeKey,
				defaultExpandedKeys,
				defaultCheckedkeys
			}"
			v-on="{
				...$listeners,
				'current-change':currentChange,
				'node-expand':nodeExpand,
				'node-collapse':nodeCollapse,
				check
			}"
		/>
	</section>
</template>

<script>
import { clone, getTreeNodeParent } from '../../src/utils'

export default {
	name: 'UiTree',
	props: {
		bind: {
			type: Object,
			default: _ => ({})
		}
	},
	data() {
		return {
			currentNodeKey: '',
			defaultExpandedKeys: [],
			defaultCheckedkeys: [],
			scrollTop: 0
		}
	},
	watch: {
		'bind.currentNodeKey': {
			immediate: true,
			handler(val) { this.currentNodeKey = val }
		},
		'bind.defaultExpandedKeys': {
			immediate: true,
			handler(val, old) {
				if (
					(val || []).toString() != (old || []).toString()
				) this.defaultExpandedKeys = clone(val) || []
			}
		},
		'bind.defaultCheckedKeys': {
			immediate: true,
			handler(val, old) {
				if (
					(val || []).toString() != (old || []).toString()
				) this.defaultCheckedkeys = clone(val) || []
			}
		},
		'bind.data'() {
			this.$nextTick(_ => {
				this.$el.scrollTop = this.scrollTop
				this.$refs.tree.setCurrentKey(this.currentNodeKey)
				this.$refs.tree.setCheckedKeys(this.defaultCheckedkeys)
			})
		},
		currentNodeKey: {
			immediate: true,
			handler(val) {
				this.$emit('update-current-node-key', val)
				this.$nextTick(_ => {
					this.$refs.tree.setCurrentKey(val)
					this.scrollTo(val)
				})
			}
		}
	},
	methods: {
		scroll(e) {
			this.scrollTop = e.target.scrollTop
		},
		scrollTo(key) {
			const fn = _ => {
				const
					rect1 = this.$el.getBoundingClientRect(),
					rect2 = this.$el.querySelector('.el-tree-node.is-current>.el-tree-node__content').getBoundingClientRect(),
					d_top = rect2.top - rect1.top,
					d_bottom = rect2.bottom - rect1.bottom,
					d_center = (rect2.top + rect2.bottom - rect1.top - rect1.bottom) / 2

				this.$el.scrollTop += Math.max(-this.$el.scrollTop, d_top < 0 || d_bottom > 0 ? d_center : 0)
			}

			clearTimeout(this.timer)

			new Promise(resolve => {
				const node = _ => this.$refs.tree && this.$refs.tree.getNode(key),
					dom = _ => this.$el.querySelector('.el-tree-node.is-current>.el-tree-node__content')

				if (node() && dom()) resolve()
				else this.timer = setTimeout(_ => node() && dom() && resolve(), 1000)
			}).then(_ => fn())
		},
		currentChange(data, node) {
			this.$emit('current-change', data, node)
			this.currentNodeKey = node.key
		},
		nodeExpand(data, node, vm) {
			this.$emit('node-expand', data, node, vm)
			const { defaultExpandedKeys } = this
			this.defaultExpandedKeys = Array.from(new Set([...defaultExpandedKeys, ...getTreeNodeParent(node).map(node => node.key)]))
		},
		nodeCollapse(data, node, vm) {
			this.$emit('node-collapse', data, node, vm)
			const { defaultExpandedKeys } = this,
				all_keys = getTreeNodeParent(node).map(node => node.key),
				key = all_keys.pop()
			this.defaultExpandedKeys = Array.from(new Set([...defaultExpandedKeys.filter(id => id != key), ...all_keys]))
		},
		check(data, state) {
			this.$emit('node-collapse', data, state)
			this.defaultCheckedkeys = state.checkedKeys
		}
	}
}
</script>