<script>
import uiAutoFormItem from '../autoFormItem'
import locale from '../../src/mixins/locale'
import { clone } from '../../src/utils'
export default {
	name: 'UiFormDialog',
	components: { uiAutoFormItem },
	mixins: [locale],
	props: {
		visible: {
			type: Boolean,
			default: false
		},
		title: {
			type: String,
			default: ''
		},
		width: {
			type: String,
			default: '500px'
		},
		autoHeight: {
			type: Boolean,
			default: false
		},
		inline: {
			type: Boolean,
			default: false
		},
		labelPosition: {
			type: String,
			default: 'right'
		},
		labelWidth: {
			type: String,
			default: '100px'
		},
		readonly: {
			type: Boolean,
			default: false
		},
		disabled: {
			type: Boolean,
			default: false
		},
		form: {
			type: Object,
			default: _ => ({})
		},
		formConf: {
			// 表单各字段配置
			type: Object,
			default: _ => ({})
		},
		submit: {
			// 提交函数
			type: Function,
			default: vm => ({})
		},
		temp: {
			type: Object,
			default: _ => ({})
		},
		// 隐藏提交按钮
		hiddenSubmit: {
			type: Boolean,
			default: false
		}
	},
	data() {
		return {
			inputDisabled: false,
			submiting: false,
			selfFormConf: {},
			selfForm: {}
		}
	},
	computed: {
		forbidden() {
			const { readonly, disabled, inputDisabled } = this
			return readonly || disabled || inputDisabled
		}
	},
	watch: {
		formConf: {
			deep: true,
			immediate: true,
			handler(val) {
				this.selfFormConf = clone(val)
			}
		},
		form: {
			deep: true,
			immediate: true,
			handler(val) {
				this.selfForm = clone(val)
			}
		}
	},
	created() {
		this.initializeModel()
	},
	methods: {
		assure() {
			this.$refs.form.validate(valid => {
				if (valid) {
					this.inputDisabled = true
					this.submiting = true
					Promise.resolve(this.submit(this.selfForm, this)).then(
						_ => this.$emit('update:visible', false),
						_ => {
							this.submiting = false
							this.inputDisabled = false
						}
					)
				}
			})
		},
		// 初始化form,若formConf的各项属性在form不存在对应，则进行填充
		initializeModel() {
			const { selfForm, selfFormConf } = this
			Object.keys(selfFormConf).forEach(key => {
				const origin_value = selfForm[key],
					config_value = selfFormConf[key].value == undefined ? null : clone(selfFormConf[key].value)
				this.$set(selfForm, key, origin_value == undefined ? config_value : origin_value)
			})
		}
	},
	render(h) {
		const { selfFormConf: formConf, $scopedSlots, selfForm: form, assure, title, visible, autoHeight, width, inline, forbidden, labelPosition, labelWidth, readonly } = this,
			formItems = Object.keys(formConf).map(key => {
				const item = formConf[key],
					{ type, label, required, rules = [], render = false, visible = true, width = item.width || 'auto', height = 'auto', 'label-width': labelWidth } = item
				return <el-form-item
					{...{
						attrs: { key, label, prop: key, required, rules, 'label-width': labelWidth },
						style: {
							display: inline ? 'inline-block' : 'block',
							width,
							height
						}
					}}
					ref={key}
					v-show={visible}
				>
					{render
						? render({ form, formConf, Self: this })
						: $scopedSlots[key]
							? $scopedSlots[key]({ form, formConf, Self: this })
							: type
								? <ui-auto-form-item
									{...{
										attrs: {
											...item,
											type,
											value: form[key]
										},
										on: {
											input: e => form[key] = e
										}
										/* nativeOn: {
											keyup: e => e.keyCode == 13 && assure()
										} */
									}}
								/>
								: null}
				</el-form-item>
			})

		return <ui-dialog
			class="ui-form-dialog"
			{...{
				props: {
					title,
					visible,
					width
				},
				attrs: {
					'auto-height': autoHeight
				},
				on: {
					'update:visible': _ => this.$emit('update:visible', false)
				}
			}}
		>
			<el-form
				{...{
					props: {
						disabled: forbidden,
						model: form,
						labelPosition,
						labelWidth,
						inline
					}
				}}
				style="height:100%;"
				ref="form"
				size="mini"
			>
				<input type="text" style="display: none;" />
				{formItems}
			</el-form>
			<span class="dialog-footer" slot="footer">
				<el-button on={{ click: _ => this.$emit('update:visible', false) }} size="mini">
					{this.t('ui.formDialog.close')}
				</el-button>
				<el-button
					disabled={forbidden}
					on={{
						click: _ => assure()
					}}
					size="mini"
					type="primary"
					v-show={!readonly}
					icon={this.submiting ? 'el-icon-loading' : ''}
				>
					{this.submiting ? this.t('ui.formDialog.submiting') : this.t('ui.formDialog.submit')}
				</el-button>
			</span>
		</ui-dialog>
	}
}
</script>

<style lang="scss" scoped>
.ui-form-dialog > .el-dialog > .el-dialog__body > .el-form > .el-form-item {
	display: inline-block;
	margin-right: 0;

	& > .el-form-item__content {
		height: 100%;
	}
}
</style>