// 日期格式化方法
Date.prototype.format = function(fmt) {
	const o = {
		'M+': this.getMonth() + 1, // 月份
		'd+': this.getDate(), // 日
		'h+': this.getHours(), // 小时
		'm+': this.getMinutes(), // 分
		's+': this.getSeconds(), // 秒
		'q+': Math.floor((this.getMonth() + 3) / 3), // 季度
		'S+': this.getMilliseconds() // 毫秒
	}
	if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (this.getFullYear() + '').substr(4 - RegExp.$1.length))
	for (const k in o)
		if (new RegExp('(' + k + ')').test(fmt)) fmt = fmt.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : ('00' + o[k]).substr(-RegExp.$1.length))
	return fmt
}

// 数组替换方法
Array.prototype.replace = function(...args) {
	this.splice(0, this.length, ...args)
	return this
}

// 跨级获取嵌套对象属性值
Object.defineProperty(Object.prototype, 'deepGet', {
	writable: false,
	enumerable: false,
	configurable: true,
	value(...args) {
		if (args.some(item => ['string', 'number'].indexOf(typeof item) < 0)) throw new Error('参数类型只能是string、number！')
		return args.reduce((res, item) => (res || {})[item], this)
	}
})

// 跨级设置对象属性值
Object.defineProperty(Object.prototype, 'deepSet', {
	writable: false,
	enumerable: false,
	configurable: true,
	value(...args) {
		if (args.length < 2) throw new Error('参数个数至少为2！')

		const last1 = args.pop()

		if (args.some(item => ['string', 'number'].indexOf(typeof item) < 0)) throw new Error('除末尾参数外，其余只能是string、number！')

		const last2 = args.pop(),
			obj = args.reduce((res, item) => {
				if (!res[name]) res[name] = {}
				if (Object.prototype.toString.call(res[name]) != '[object Object]') throw new Error('中间节点格式错误！')
				return res[name]
			}, this)

		obj[last2] = last1
	}
})

// 克隆数据
function clone(a) {
	let copy
	switch (Object.prototype.toString.call(a)) {
		case '[object Object]':
			copy = {}
			for (const i in a) {
				copy[i] = clone(a[i])
			}
			break
		case '[object Array]':
			copy = []
			a.forEach(v => {
				copy.push(clone(v))
			})
			break
		case '[object Date]':
			copy = new Date(a.getTime())
			break
		/* case '[object RegExp]':

	break; */
		default:
			copy = a
	}
	return copy
}

// 标准化对象
function standard(value, format) {
	const _type = Object.prototype.toString.call(value),
		type = Object.prototype.toString.call(format)

	if (_type != type) return clone(format)

	let a
	switch (type) {
		case '[object Object]':
			a = { ...value }
			for (const i in format) {
				a[i] = standard(a[i], format[i])
			}
			break
		case '[object Array]':
			a = [...value]
			format.forEach((v, i) => {
				if (a.length < i + 1) a.push(v)
				a[i] = standard(a[i], v)
			})
			break
		default:
			a = clone(value)
	}

	return a
}

// 获取数据类型
function getType(v) {
	return Object.prototype.toString.call(v).match(/^\[object (\w+)\]$/)[1]
}

/*
防抖函数
	immediate: true:立即执行，false:wait(ms)之后执行
*/
function debounce(func, wait, immediate = true) {
	let timeout

	return function() {
		const context = this,
			args = arguments

		if (timeout) clearTimeout(timeout)
		if (immediate) {
			const callNow = !timeout
			timeout = setTimeout(() => {
				timeout = null
			}, wait)
			if (callNow) func.apply(context, args)
		} else {
			timeout = setTimeout(function() {
				func.apply(context, args)
			}, wait)
		}
	}
}

/*
节流函数：
	immediate: true:立即执行，false:wait(ms)之后执行
*/
function throttle(func, wait, immediate = true) {
	let previous = 0,
		timeout

	return function() {
		const context = this,
			args = arguments
		if (immediate) {
			const now = Date.now()

			if (now - previous > wait) {
				func.apply(context, args)
				previous = now
			}
		} else {
			if (!timeout) {
				timeout = setTimeout(() => {
					timeout = null
					func.apply(context, args)
				}, wait)
			}
		}
	}
}

// 判断电脑还是手机端
function isPC() {
	return !['Android', 'iPhone', 'SymbianOS', 'Windows Phone', 'iPad', 'iPod'].some(v => navigator.userAgent.includes(v))
}

// 随机字符
function randomString() {
	return new Date().getTime() + Math.random().toString(36).substr(2)
}

// 获取树节点父级
function getTreeNodeParent(node) {
	const parent = node && node.level > 1 ? getTreeNodeParent(node.parent) : []
	return [...parent, node]
}

export {
	clone,
	standard,
	getType,
	debounce,
	throttle,
	isPC,
	randomString,
	getTreeNodeParent
}