import defaultLang from './lang/zh-CN'
import Vue from 'vue'
import Format from 'element-ui/lib/locale/format'

const format = Format(Vue)
let lang = defaultLang,
	i18nHandler = function() {
		const vuei18n = Object.getPrototypeOf(this || Vue).$t
		return typeof vuei18n == 'function' ? vuei18n.apply(this, arguments) : null
	}

export const t = function(path, options) {
	const value = i18nHandler.apply(this, arguments)

	return value === null
		? format(path.split('.').reduce((total, value, index, arr) => total[value], lang), options)
		: value
}

export const use = function(l) {
	lang = l || lang
}

export const i18n = function(fn) {
	i18nHandler = fn || i18nHandler
}

export default { use, t, i18n }